@import "variables";

@mixin make-list-compatible-button {
  border: none;
  background-image: none;
  background-color: transparent;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  box-shadow: none;
  width: 100%;
  height: 100%;
  color: $lightTypeTwo;
  cursor: pointer; }

@mixin reset-list {
  margin: 0;
  padding: 0;
  list-style: none; }

@mixin add-styled-scroll {
  scrollbar-width: thin;
  scrollbar-color: $teamOrange $midDark;
  &::-webkit-scrollbar-track {
    background: $midDark; }
  &::-webkit-scrollbar-thumb {
    background-color: $teamOrange;
    border-radius: 20px;
    border: 3px solid $teamOrange; } }


@mixin items-are-flex {
  & > li {
    display: flex; } }

@mixin buttons-held-by-items-are-reset {
  & > li > button {
    @include make-list-compatible-button; } }

@mixin bottom-shadow {
  box-shadow: 0 1px 2px 2px $mainShadowColor; }

@mixin right-shadow {
  box-shadow: 1px 0 2px 2px $mainShadowColor; }

@mixin surround-shadow {
  box-shadow: 1px 1px 2px 2px $mainShadowColor; }

@mixin flex-column-containment {
  display: flex;
  flex-direction: column;
  & > * {
    margin-bottom: 0.5em; } }
